
.portfolio-card{
    /* padding: 10px; */
    margin-bottom: 10px;
    height: 100% !important;
    max-width: 750px;
    align-self: center;
    flex-direction: column;
}

@media (min-width: 0) and (max-width: 1190) {
    .portfolio-card{
        display: flex; 
        height: 100% !important;
        max-width: 100% !important;
        flex-direction: row;
    }
}

.hover-zoom-p{
    transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -webkit-transition: all 0.1s ease-in-out;
    cursor: default;
}

.hover-zoom-p:hover{
    transform: scale(1.4) !important;
}



.tech-img-portfolio:hover .tech-img-portfolio-tooltip{
    visibility: visible;
}

.tooltip-cls {
    position: relative;
    display: inline-block;
}

.tooltip-cls .tech-img-portfolio-tooltip {
    visibility: hidden;
    width: 250px;
    background-color: black;
    color: #fff;
    text-align: start;
    border-radius: 6px;
    padding: 0px 8px 0px 8px;
    position: absolute;
    top: 120%;
    left:-50%;
    z-index: 140 !important;
  }