.home-img{
    background: linear-gradient( 
            3420deg
         , transparent 60%, white 90%);
    height: 501px;
    width: 100%;
    min-width: 100% !important;
    position: absolute;
    top: 0;
}

.hover-zoom{
    transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -webkit-transition: all 0.1s ease-in-out;
    cursor: default;
}
.home-title{
    cursor: default;
}

.hover-zoom:hover{
    transform: scale(1.1) !important;
}

.insta-social{
    width: 50px;
    height: 50px;
    padding: 10px;
}

.social-media a{
    text-decoration: none;
    color: black
}
.social-media > a:hover{
    text-decoration: none;
}

.css-selector {
    background: linear-gradient(89deg, #990000, #ffcc00);
    background-size: 400% 400%;

    -webkit-animation: coolbuttons 30s ease infinite;
    -moz-animation: coolbuttons 30s ease infinite;
    animation: coolbuttons 30s ease infinite;
}

@-webkit-keyframes coolbuttons {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes coolbuttons {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes coolbuttons {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}

.desktop-container{
    display: flex;
}
.mobile-container{
    display: none;
}
.buttons-below-profile-photo{
    width: 390px;
    left: 30px;
    top: 660px;
    position: absolute !important;
    /* margin-left: 30px; */
}

.portfolio-container{
    position: absolute;
    top: 40vw;
    width: 100% !important;
}

@media screen and (max-width: 1190px) {
    
    .desktop-container{
        display: none !important;
    }
    .mobile-container{
        display: flex !important;
    }
    .buttons-below-profile-photo{
        left: 0px;
        top: 65vw !important;
        width: 100% !important;
        padding-left: 0 !important;
    }

    .profile-photo{
        width: 100% !important;
    }

    
    .portfolio-container{
        top: 75vw;
    }

}



@media (min-width: 1191px) and (max-width: 1480px) {
    .portfolio-container{
        top: 60vw !important;
    }
}


@media (min-width: 1481px) and (max-width: 1570px) {
    .portfolio-container{
        top: 50vw !important;
    }
}


@media (min-width: 1571px) and (max-width: 1720px) {
    .portfolio-container{
        top: 48vw !important;
    }
}


@media (min-width: 1721px) and (max-width: 1980px) {
    .portfolio-container{
        top: 44vw !important;
    }
}


.contact-me-button:hover .contact-me-tooltip{
    visibility: visible;
}

.tooltip-cls {
    position: relative;
    display: inline-block;
}

.tooltip-cls .contact-me-tooltip {
    visibility: hidden;
    width: auto;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 0px 5px;
    position: absolute;
    top: -100%;
    left:-100%;
    z-index: 140 !important;
  }